import { vec2, vec3, mat4 } from 'gl-matrix';
import { } from '../../lib';

export class Camera {
    public viewMatrix = mat4.create();
    public projectionMatrix = mat4.create(); 
    public viewProjectionMatrix = mat4.create();

    public zoomMax = 100;
    public zoomSpeed = 2;

    public near = 0.01;
    public far = 10000;

    public mvpMatrix = mat4.create();

    public eye = vec3.fromValues(0,0,0);
    // public eye = vec3.fromValues(0, 1, 0);
    public center = vec3.fromValues(0, 0, 0);
    public up = vec3.fromValues(0, 1, 0);

    public type ='camera';

    public respectRatio: number;
    public width: number;
    public height: number;

    public angleXY = Math.PI; 
    public angleZ = Math.PI * 0.25;
    public distance = 1;

    constructor(width: number = 100, height: number = 100, cameraPosition: vec3 = [2, 2, 4], center: vec3 = [0, 0, 0],  up: vec3 = [0, 1, 0], distance: number = 5) {
        this.distance = distance; this.width = width; this.height = height; this.respectRatio = width / height
        this.eye = cameraPosition;
        this.center = center;
        this.up = up;
    }
    public zoom(v: number) { 
        //
    }
    public update() {
        mat4.lookAt(this.viewMatrix, this.eye, this.center, this.up);
        mat4.multiply(this.viewProjectionMatrix, this.projectionMatrix, this.viewMatrix);

        // createTransforms(this.modelMatrix, this.translation, this.rotation);
        // mat4.multiply(this.mvpMatrix, this.viewProjectionMatrix, this.viewMatrix);
        // console.log(this.type);
    }
    
}

export class CameraPerspective extends Camera {
    constructor(width = 100, height = 100, cameraPosition: vec3 = [2, 2, 4], center: vec3 = [0, 0, 0],  up: vec3 = [0, 1, 0], distance = 3){
        super(width, height, cameraPosition, center, up, distance);
        this.type = 'CameraPerspective';
        this.init();
    }
    public init() {
        mat4.perspective(this.projectionMatrix, 2 * Math.PI / 5, this.respectRatio, this.near, this.far);
        this.update();
    }
    public zoom (v: number ) {
        this.distance += v * 0.01;
    }

}
export class CameraOrth extends Camera {
    constructor(width = 100, height = 100, cameraPosition: vec3 = [0 ,0, 1], center: vec3 = [0, 0, 0],  up: vec3 = [0, 1, 0], distance = 300.1){
        super(width, height, cameraPosition, center, up, distance);
        this.type = 'CameraOrth';
        this.init()
    }
    public init() {
        projection(this.projectionMatrix, this.width, this.height, this.near , this.far, this.distance);

        // const aspectRatio = width / height;
        // const zoomFactor = 0.1; //this.distance;
        // const projectionMatrix = mat4.ortho(
        //   -aspectRatio * zoomFactor,
        //   aspectRatio * zoomFactor,
        //   -1 * zoomFactor,
        //   1 * zoomFactor,
        //   0.1,
        //   100.0
        // );

        // mat4.ortho(this.projectionMatrix, -aspectRatio * zoomFactor, aspectRatio * zoomFactor,  -1 * zoomFactor, 1 * zoomFactor, this.near, this.far);
        // mat4.ortho(this.projectionMatrix, 0, 500, 800, 0, 0.1, 5000);
        // mat4.ortho(this.projectionMatrix, 0, 0, 800, 800, -400 , 400);
        // mat4.frustum(this.projectionMatrix, 0, 800, 800, 0, -400, 400);


        this.angleXY = 0;
        this.angleZ = 0;
        this.update();
    }
    public zoom(v: number) {
        this.distance += v + 50 / this.distance;
        if (this.distance < 0.000001) { this.distance = 0.000001; }
        if (this.distance > 100000) { this.distance = 100000; }
        projection(this.projectionMatrix, this.width, this.height, this.near, this.far, this.distance);
    }
}

const projection = (dst, width, height, near , far, scale) => {
    // Note: This matrix flips the Y axis so that 0 is at the top.
    // dst = dst || new Float32Array(16);
    dst[ 0] = 2 / width * scale;    dst[ 1] = 0;                    dst[ 2] = 0;                        dst[ 3] = 0;
    dst[ 4] = 0;                    dst[ 5] = 2 / height * scale;   dst[ 6] = 0;                        dst[ 7] = 0;
    dst[ 8] = 0;                    dst[ 9] = 0;                    dst[10] = -2 / (far - near);       dst[11] = 0;
    dst[12] = 0;                    dst[13] = 0;                    dst[14] = 0.1;                      dst[15] = 1;
    // dst[12] = -1;                dst[13] = 1;                    dst[14] = 0.5;                      dst[15] = 1;
    return dst;
};

