import { vec2, vec3, mat4 } from 'gl-matrix';
import { Camera, CameraPerspective, CameraOrth } from './Cameras';
import { Renderer } from '..';

export class CameraController {
    public solution: Renderer;
    public canvas: HTMLCanvasElement

    public cameraOrth: Camera;
    public cameraPerspective: Camera;

    public camera: Camera;

    private modelMatrix: mat4 = mat4.create();
    private mvpMatrix: mat4 = mat4.create();

    // public vMatrix: mat4;
    // public vpMatrix: mat4;
    public rotation =vec3.fromValues(0, 0, 0); 
    public translation =vec3.fromValues(0, 0, 0);
    public screen2 = vec2.fromValues(0, 0);

    // public projectionMatrix = mat4.create();

    // public eye = vec3.fromValues(0,10,0);
    // angleXY 1.9300000000000026 angleZ -0.47999999999999976 5 distance

    // angleXY 2.6400000000000046 angleZ 0.6799999999999998 3.3800000000000017 distance
    // src_njslab_Infrastructure_WebGPU_WebGPU_NJS_Sandbox_01_index_ts.chunk.js:1121 camera.eye Float32Array(3)[1.2636959552764893, -2.3044490814208984, 2.1253204345703125,


    constructor(solution: Renderer) {
        this.solution = solution;
        this.canvas = solution.canvas;

        const eye = vec3.fromValues(2.6451475620269775, 3.130298137664795, 2.864337205886841); // 1.6399056911468506, 1.1976807117462158, 1.162699580192566
        // const eye = vec3.fromValues(10, -10, 2);
        const center = vec3.fromValues(0, 0, 0);
        const up = vec3.fromValues(0, 0, 1);

        this.cameraPerspective = new CameraPerspective(this.canvas.width, this.canvas.height, eye, center, up);
        this.camera = this.cameraPerspective; 
        this.arcRotation(0, 0);

        this.cameraOrth = new CameraOrth(this.canvas.width, this.canvas.height);
        this.camera = this.cameraOrth; // this.cameraPerspective // this.cameraOrth;

        this.update();
    }
    public update() {
        // mat4.lookAt(this.vMatrix, this.eye, this.center, this.up);
        this.camera.update();

        // mat4.multiply(this.camera.viewProjectionMatrix, this.camera.projectionMatrix, this.camera.viewMatrix);
        createTransforms(this.modelMatrix, this.translation, this.rotation);
        mat4.multiply(this.mvpMatrix, this.camera.viewProjectionMatrix, this.modelMatrix);
        // console.log('camera updated...');
    }
    public rotateZ(val = 0.1) {
        this.rotation[2] += val;
        this.update();
    }
    public translateX(val = 0.1) {
        this.translation[0] += val;
        this.update();
    }
    public get getMatrix() {
        return this.mvpMatrix;
    }
    public shiftCamera() {
        // console.log('shiftCamera');
        if (this.camera === this.cameraPerspective){
            this.camera = this.cameraOrth;
        } else {
            this.camera = this.cameraPerspective;
        }
        this.update();
    }
    public moveCamera(x: number, y: number) {
        if (this.camera === this.cameraPerspective){
            this.arcRotation(x, y);
        } else {
            this.pan(x, y);
        }
    }
    public arcRotation(x: number, y: number) {
        x = x * -0.01;
        y = y * -0.01;
        this.camera.angleXY += x;
        this.camera.angleZ += y;
        this.camera.eye[0] = this.camera.center[0] + (Math.sin(this.camera.angleXY) * Math.cos(this.camera.angleZ)) * this.camera.distance;
        this.camera.eye[1] = this.camera.center[1] + (Math.cos(this.camera.angleXY) * Math.cos(this.camera.angleZ)) * this.camera.distance;
        this.camera.eye[2] = this.camera.center[2] + (Math.sin(this.camera.angleZ) * this.camera.distance);

        // console.log('angleXY', this.camera.angleXY, 'angleZ', this.camera.angleZ, this.camera.distance, 'distance' )
        // console.log('camera.eye', this.camera.eye )


        this.update();
    }
    public zoomWheel(v: number) {

        if (this.camera === this.cameraPerspective){
            this.camera.zoom(v)
            this.arcRotation(0, 0);
        } else {
            this.camera.zoom(v)
        }
        
        // 

        // console.log(this.scale);

        this.update();
    }
    public pan(x: number, y: number) {
        const scale = 0.01
        this.camera.center[0] += x / this.camera.distance ;
        this.camera.center[1] -= y / this.camera.distance;
        this.camera.eye[0] += x / this.camera.distance;
        this.camera.eye[1] -= y / this.camera.distance;
        this.update();

    }
    // public arcRotation(x: number, y: number) {
    //     x = x * 0.01;
    //     y = y * 0.01;
    //     this.angleXY += x;
    //     this.angleZ += y;
    //     this.camera.eye[0] = this.camera.center[0] + (Math.sin(this.angleXY) * Math.cos(this.angleZ)) * this.distance;
    //     this.camera.eye[1] = this.camera.center[1] + (Math.cos(this.angleXY) * Math.cos(this.angleZ)) * this.distance;
    //     this.camera.eye[2] = this.camera.center[2] + (Math.sin(this.angleZ) * this.distance);

    //     console.log('angleXY', this.angleXY, 'angleZ', this.angleZ, this.distance, 'distance' )
    //     console.log('camera.eye', this.camera.eye )
    //     this.update();
    // }
}

const createTransforms = (modelMat:mat4, translation:vec3 = [0,0,0], rotation:vec3 = [0,0,0], scaling:vec3 = [1,1,1]) => {
    const rotateXMat = mat4.create();
    const rotateYMat = mat4.create();
    const rotateZMat = mat4.create();   
    const translateMat = mat4.create();
    const scaleMat = mat4.create();

    //perform individual transformations
    mat4.fromTranslation(translateMat, translation);
    mat4.fromXRotation(rotateXMat, rotation[0]);
    mat4.fromYRotation(rotateYMat, rotation[1]);
    mat4.fromZRotation(rotateZMat, rotation[2]);
    mat4.fromScaling(scaleMat, scaling);

    //combine all transformation matrices together to form a final transform matrix: modelMat
    mat4.multiply(modelMat, rotateXMat, scaleMat);
    mat4.multiply(modelMat, rotateYMat, modelMat);        
    mat4.multiply(modelMat, rotateZMat, modelMat);
    mat4.multiply(modelMat, translateMat, modelMat);
};