import { App } from '..';
import { CameraController } from './Camera/CameraController';
import { WebGPU } from './WebGPU';

export class Renderer {
    public app: App;
    public canvas: HTMLCanvasElement;

    public cameraController: CameraController;
    public webGPU: WebGPU;

    public isAnimation: boolean = true;
    private requestAni: any;

    constructor(app: App) {
        this.app = app;

        this.canvas = document.createElement('canvas');
        // this.canvas.style.width = '100px';
        // this.canvas.style.height = '100px';
        this.canvas.width = this.app.divHost.clientWidth;
        this.canvas.height = this.app.divHost.clientHeight;
        this.app.divHost.appendChild(this.canvas);
        
        // window.addEventListener('resize', (ev: UIEvent) => {
        //     console.log(ev)
        //     this.canvas.width = this.app.divHost.clientWidth;
        //     this.canvas.height = this.app.divHost.clientHeight;
        //     // this.canvas.width = e
        // });

        this.webGPU = new WebGPU(this);
    }
    async init() {
        await this.webGPU.initWebGPU();

        this.cameraController = new CameraController(this);
        this.requestAni = requestAnimationFrame(this.loop);
    }

    public loop = () => {
        if (this.isAnimation) {
            this.requestAni = requestAnimationFrame(this.loop);
        }
        // this.cameraController.update();
        this.drawing();
    }
    public drawing() {
        this.app.scene.render();
    }
    public destroy() {
        this.webGPU.destroy();
        // this.isAnimation = false;
        cancelAnimationFrame(this.requestAni);
        // this.requestAni = undefined;
        while (this.app.divHost.lastElementChild) {
            this.app.divHost.removeChild(this.app.divHost.lastElementChild);
        }
    }
}

